	change (event) {
		const { options } = event.target;
		const value = [];
		for (let i = 0, l = options.length; i < l; i += 1) {
		  if (options[i].selected) {
			value.push(options[i].value);
		  }
		}
		this.setState({value})
	}

	onChange (e) {
		const {name, value, type, checked} = e.target
		// console.log(name)
		// console.log(value)
		// console.log(type)
		// console.log(checked)

		let ti = this.state.ti
		let index = ti.indexOf(value)

		if (type === 'checkbox') {
			if(checked) {
				this.setState({ti: this.state.ti.concat(value)})
			}
			else if (!checked && checked !== undefined) {
				ti.splice(index, 1)
				this.setState({ti})
			}
		} else if (type === 'radio') {
			name === 'editGroup' ? this.setState({[name]: Number(value)}) : this.setState({[name]: value})
			this.modalBack()
		}
		else
			this.setState({[name]: value})
	}

